import { useState } from "react";
import TypingAnime from "../components/TypingAnime";
import { useNavigate } from "react-router";

function Intro() {
  const navigate = useNavigate();
  const [display, setDisplay] = useState("block");

  const handleStyle = () => {
    const newDisplay = display === "block" ? "none" : "block";
    setDisplay(newDisplay);
    navigate("/main");
  };

  return (
    <div className="intro_wrap" style={{ display }}>
      <div className="intro_inner">
        <div className="intro_box">
          <div className="intro_logo"></div>
          <div className="intro_text">STUDY-HUB   .</div>
          {/* <TypingAnime text="STUDY HUB  ." /> */}
          <button onClick={handleStyle}></button>
        </div>
      </div>
    </div>
  );
}

export default Intro;
