//, ,    
export const getPlanData = async (_month, _year, _day) => {
  try {
    const res = await axios.get(
      `/api/todo/bymonth?iuser=2&month=${_month}&year=${_year}&day=${_day}`,
    );
    const result = res.data;
    // console.log("getPlan ");
    return result;
  } catch (err) {
    console.log(err);

    return [];
  }
};

