// //  
export const putPlan = async (
  planLog,
  editTitle,
  editctnt,
  selectedSubject,
) => {
  try {
    const res = await axios.put("/api/todo", {
      itodo: planLog,
      title: editTitle,
      ctnt: editctnt,
      icategory: selectedSubject,
    });
    console.log(" !!!!!!");
    const result = res.data;
    console.log(result);
  } catch (err) {
    console.log(err);
  }
};

