export const deletePlan = async itodo => {
  try {
    const res = await axios.delete(`/api/todo?itodo=${itodo}`);
    const result = res.data;

    console.log(result);
    console.log("!!!!");
    return result;
  } catch (error) {
    console.log(error);
  }
};

