const patchMemo = async (memoLog, editTitle, editctnt, _iuser) => {
  try {
    const res = await axios.patch(`/api/memo/titlectnt/${_iuser}`, {
      imemo: memoLog,
      title: editTitle,
      ctnt: editctnt,
    });
    const result = res.data;

    console.log("?");
    console.log(result);
  } catch (error) {
    console.log(error);
  }
};

