import React, { useEffect, useState } from "react";
import { Button, Input, Modal } from "antd";
import { postSeconds } from "../api/planFetch";
import { useNavigate } from "react-router-dom";

const StudyTimer = () => {
  const navigator = useNavigate();
  //  
  const [studyTime, setStudyTime] = useState("");

  // 
  const [seconds, setSeconds] = useState(0);
  // 
  const [minutes, setMinutes] = useState(0);
  // 
  const [hours, setHours] = useState(0);
  //  
  const [isRunning, setIsRunning] = useState(false);
  //  
  const [isPaused, setIsPaused] = useState(false);
  //  
  const [isStopped, setIsStopped] = useState(false);

  //  
  useEffect(() => {
    let interval = null;
    if (isRunning) {
      interval = setInterval(() => {
        setSeconds(prevSeconds => prevSeconds + 1);
      }, 1000);
    }
    return () => clearInterval(interval);
  }, [isRunning]);

  //   
  useEffect(() => {
    if (seconds === 60) {
      setSeconds(0);
      setMinutes(prevMinutes => prevMinutes + 1);
    }
    if (minutes === 60) {
      setMinutes(0);
      setHours(prevHours => prevHours + 1);
    }
    setStudyTime(
      hours.toString().padStart(2, "0") +
        ":" +
        minutes.toString().padStart(2, "0") +
        ":" +
        seconds.toString().padStart(2, "0"),
    );
  }, [seconds, minutes]);

  //  
  const handleReset = () => {
    setIsRunning(false);
    setIsPaused(false);
    setIsStopped(false);
    setSeconds(0);
    setMinutes(0);
    setHours(0);
  };

  // 
  const handleStart = () => {
    handleReset();
    //  
    setIsRunning(true);
    setIsPaused(false);
    setIsStopped(false);
  };
  // 
  const handlePause = () => {
    setIsRunning(false);
    setIsPaused(true);
    setIsStopped(false);
  };

  //  
  const handleStop = () => {
    setIsRunning(false);
    setIsPaused(false);
    setIsStopped(true);
    const timeArr = studyTime.split(":");
    const totalSeconds =
      parseInt(timeArr[0]) * 3600 +
      parseInt(timeArr[1]) * 60 +
      parseInt(timeArr[2]);
    // console.log(totalSeconds);
    // axios   ()  .
    postSeconds({
      studyLine: totalSeconds,
      iuser: 2,
    });
    //  
    showModal();
  };

  //    
  const handleResume = () => {
    setIsRunning(true);
    setIsPaused(false);
    setIsStopped(false);
  };

  //   
  const [isModalOpen, setIsModalOpen] = useState(false);
  const showModal = () => {
    setIsModalOpen(true);
  };
  const handleOk = () => {
    setIsModalOpen(false);
    navigator("/caledar");
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <>
      <div className="timer">
        <div className="time_box">
          <Input
            value={studyTime}
            placeholder="00:00:00"
            style={{
              textAlign: "center",
              borderRadius: "unset",
              borderStyle: "unset",
              width:'200px',
              height:'50px',
              fontSize: '30px',
              
            }}
          />
        </div>
        <div className="time_select">
          {!isRunning && !isStopped && !isPaused && (
            <div>
              <Button onClick={handleStart} style={{ borderRadius: "25px" }}>
                
              </Button>
            </div>
          )}
          {isRunning && !isStopped && !isPaused && (
            <div>
              <Button onClick={handlePause} style={{ borderRadius: "25px" }}>
                
              </Button>
              <Button onClick={handleStop} style={{ borderRadius: "25px" }}>
                
              </Button>
            </div>
          )}
          {!isRunning && !isStopped && isPaused && (
            <div>
              <Button onClick={handleResume} style={{ borderRadius: "25px" }}>
                
              </Button>
              <Button onClick={handleStop} style={{ borderRadius: "25px" }}>
                
              </Button>
            </div>
          )}
          {!isRunning && isStopped && !isPaused && (
            <div>
              <Button onClick={handleStart} style={{ borderRadius: "25px" }}>
                
              </Button>
            </div>
          )}
        </div>
      </div>
      {/*  */}
      <Modal
        title=" "
        open={isModalOpen}
        onOk={handleOk}
        onCancel={handleCancel}
        // centered
        footer={[
          <Button key="back" onClick={handleOk}>
            
          </Button>,
          <Button key="close" type="primary" onClick={handleCancel}>
            
          </Button>,
        ]}
      >
          . ?
      </Modal>
    </>
  );
};

export default StudyTimer;